.cafe-section {
  background-color: rgba(66, 131, 124, 0.5);
  padding: 144px 0 200px;
  position: relative;
}

.cafe-title {
  font-size: 100px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 150px;
  color: var(--color-white);
}

.menu-list {
  display: flex;
  flex-direction: column;
  gap: 100px;
  margin-bottom: 128px;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.menu-item {
  display: flex;
  align-items: center;
  gap: 20px;
  font-size: 35px;
  font-weight: 700;
  color: var(--color-white);
  padding: 20px 0;
}

.item-name {
  flex-shrink: 0;
  min-width: 200px;
}

.item-divider {
  flex-grow: 1;
  border-bottom: 1px solid var(--color-white);
  margin: 0 20px;
}

.item-price {
  flex-shrink: 0;
  width: 100px;
  text-align: right;
}

.cart-icon {
  width: 60px;
  height: 60px;
  margin-left: 70px;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.cart-icon:hover {
  transform: scale(1.1);
}

.quantity-stepper {
  display: flex;
  align-items: center;
  background-color: var(--color-white);
  color: var(--color-black);
  width: 145px;
  height: 60px;
  border-radius: 5px;
  overflow: hidden;
  margin-left: 20px;
}

.stepper-btn {
  background: none;
  border: none;
  color: var(--color-black);
  font-size: 35px;
  font-weight: 700;
  cursor: pointer;
  width: 45px;
  height: 100%;
  text-align: center;
  transition: background-color 0.2s ease;
}

.stepper-btn:hover {
  background-color: #e0e0e0;
}

.stepper-btn:active {
  background-color: #d0d0d0;
}

.quantity {
  background-color: #d9d9d9;
  width: 55px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 24px;
}

.cafe-actions {
  display: flex;
  justify-content: center;
  gap: 86px;
  flex-wrap: wrap;
}

.cafe-actions .btn {
  min-width: 250px;
}

@media (max-width: 1200px) {
  .cafe-title {
    font-size: 80px;
    margin-bottom: 100px;
  }
  
  .menu-item {
    font-size: 28px;
  }
  
  .cart-icon {
    margin-left: 30px;
    width: 50px;
    height: 50px;
  }
  
  .quantity-stepper {
    width: 130px;
    height: 50px;
  }
  
  .stepper-btn {
    font-size: 28px;
    width: 40px;
  }
  
  .quantity {
    width: 50px;
    font-size: 20px;
  }
  
  .cafe-actions {
    gap: 40px;
  }
}

@media (max-width: 768px) {
  .cafe-section {
    padding: 60px 0;
  }
  
  .cafe-title {
    font-size: 48px;
    margin-bottom: 60px;
  }
  
  .menu-list {
    gap: 40px;
    margin-bottom: 60px;
  }
  
  .menu-item {
    font-size: 20px;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 10px;
    padding: 15px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
  }
  
  .item-name {
    min-width: auto;
    order: 1;
  }
  
  .item-divider {
    display: none;
  }
  
  .item-price {
    order: 2;
    width: auto;
  }
  
  .cart-icon {
    order: 3;
    margin-left: auto;
    width: 40px;
    height: 40px;
  }
  
  .quantity-stepper {
    order: 4;
    width: 120px;
    height: 40px;
    margin-left: 0;
  }
  
  .stepper-btn {
    font-size: 20px;
    width: 35px;
  }
  
  .quantity {
    width: 50px;
    font-size: 18px;
  }
  
  .cafe-actions {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }
  
  .cafe-actions .btn {
    min-width: 200px;
    width: 100%;
    max-width: 300px;
  }
}

@media (max-width: 480px) {
  .cafe-title {
    font-size: 36px;
  }
  
  .menu-item {
    font-size: 18px;
  }
  
  .quantity-stepper {
    width: 100px;
    height: 35px;
  }
  
  .stepper-btn {
    font-size: 18px;
    width: 30px;
  }
  
  .quantity {
    width: 40px;
    font-size: 16px;
  }
}