.hero-section {
    position: relative;
    min-height: 1042px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 100px 0;
}

.hero-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}

.hero-background img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.hero-background::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.63);
}

.hero-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 100px;
}

.hero-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 53px;
}

.hero-subtitle {
    font-weight: 200;
    font-size: 50px;
    line-height: 1.2;
    max-width: 700px;
}

.hero-title {
    font-weight: 700;
    font-size: 50px;
    line-height: 1.2;
}

.hero-info-card {
    background: var(--gradient-card);
    border-radius: 30px;
    backdrop-filter: blur(5px);
    box-shadow: inset 0px 4px 50px 10px rgba(66, 131, 124, 0.6), 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    width: 100%;
    max-width: 705px;
    min-height: 526px;
    padding: 52px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    gap: 60px;
}

.hero-info-card .card-title {
    font-weight: 700;
    font-size: 50px;
    line-height: 1.2;
}

.hero-info-card .card-address {
    font-weight: 400;
    font-size: 35px;
    line-height: 1.2;
}

@media (max-width: 1280px) {
    .hero-container {
        flex-direction: column;
        text-align: center;
    }
    .hero-content {
        align-items: center;
    }
}

@media (max-width: 768px) {
    .hero-section {
        min-height: auto;
        padding: 60px 0;
    }
    .hero-subtitle, .hero-title, .hero-info-card .card-title {
        font-size: 32px;
    }
    .hero-info-card .card-address {
        font-size: 22px;
    }
    .hero-info-card {
        padding: 30px;
        gap: 30px;
    }
}